.main {
  display: block;
  margin: 0px auto;
  padding-top: 10em;
  align-items: center;
  text-align: center;
  min-height: 300px;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("./bg.png");
}
.infoCard {
  display: block;
  margin: 20px auto;
  width: 80%;
  height: auto;
  max-width: 500px;
}
.infoCard p {
  margin: 10px;
  text-align: justify;
}
.infoCard button,
.infoCard a {
  margin: 0px auto;
}
